<template>
  <div :id="getId()" class="ModalComponent">
    <div
      class="ModalComponentMask"
      :id="getId()+'Mask'"
      v-on:click="closeModalComponent($event);"
      :style="'zIndex:' +zIndexMask"
    >
      <div
        :id="getId()+'ModalContainer'"
        :class="'ModalComponentContainer '+styleClass"
        :style="'zIndex:' +zIndexModal"
      >
        <!--header-->
        <div class="ModalComponentHeader">
          <div class="ModalComponentHeaderLabel">{{title}}</div>

          <div class="ModalComponentHeaderIcon">
            <button
              type="button"
              class="ModalComponentHeaderIconButton"
              v-on:click="closeModalComponent($event, true);"
            >
              <i class="fas fa-times"></i>
            </button>
          </div>
        </div>

        <!--Body-->
        <div class="ModalComponentBody">
          <div class="ModalComponentBodyFocusable">
            <input
              :id="idFirsFocusableInput"
              class="ModalComponentBodyFocusableInput"
              v-on:focus="onFocusInput($event)"
            />
          </div>

          <div :id="getId()+'ModalComponentBodyContainer'" class="ModalComponentBodyContainer"></div>

          <div class="ModalComponentBodyFocusable">
            <input
              class="ModalComponentBodyFocusableInput"
              v-on:focus="onFocusInput($event, idFirsFocusableInput)"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";
import CommonsUtils from "../../utils/CommonsUtils";
import StringUtils from "../../utils/StringUtils";
export default {
  extends: BaseComponent,
  name: "ModalComponent",
  props: {
    title: {
      type: String,
      default: ""
    },
    styleClass: {
      type: String,
      default: ""
    },
    idComponentFocusClose: {
      type: String,
      default: null
    }
  },
  methods: {
    /**
     * Method for focus in first input
     */
    focusFirstInput: function() {
      document.getElementById(this.idFirsFocusableInput).focus();
    },
    /**
     * Method to set focus on input
     * @param event is a event produces focus
     * @param idFocusComponent is a id for component set focus
     */
    onFocusInput: function(event, idFocusComponent) {
      if (CommonsUtils.isNotNull(idFocusComponent)) {
        event.preventDefault();
        document.getElementById(idFocusComponent).focus();
      }
    },
    /**
     * Method for close modal component
     * @param event is a event produces close modal component
     * @param force if is not null and true force close modal
     */
    closeModalComponent: function(event, force) {
      if (CommonsUtils.isNull(force)) {
        force = false;
      }
      if (
        (event != null &&
          event.target.classList.contains("ModalComponentMask")) ||
        force
      ) {
        const modal = document.getElementById(this.id);
        modal.parentElement.removeChild(modal);

        if (CommonsUtils.isNotNull(this.idComponentFocusClose)) {
          let component = document.getElementById(this.idComponentFocusClose);
          if (CommonsUtils.isNotNull(component)) {
            try {
              component.focus();
              component.select();
            } catch (ex) {
              console.log(ex);
            }
          }
        }
      }
    }
  },
  created: function() {
    this.zIndexMask = this.getVueContext().getUpdatedZIndexStr();
    this.zIndexModal = this.getVueContext().getUpdatedZIndexStr();
    this.idFirsFocusableInput = StringUtils.uniqueId();
    this.idLastFocussableInput = StringUtils.uniqueId();
  },
  data() {
    return {
      // zIndex modal mask
      zIndexMask: null,
      // zIndex modal
      zIndexModal: null,
      // id first focusable input
      idFirsFocusableInput: null,
      // id last focusable input
      idLastFocussableInput: null
    };
  }
};
</script>

<style scoped>
.ModalComponent {
  position: fixed;
  height: 100%;
  width: 100%;
  background: transparent;
}

.ModalComponentMask {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
}

.ModalComponentContainer {
  position: relative;
  max-width: 85%;
  max-height: 80%;
  left: 70px;
  top: 50px;
  background-color: var(--rf-modalsBackColor, white);
  overflow-y: auto;
}

.ModalComponentHeader {
  float: left;
  width: calc(100%);
  height: 40px;
  background-color: var(--rf-modalsHeaderBackColor, #466289);
  color: var(--rf-modalsHeaderFontColor, white);
}

.ModalComponentHeaderLabel {
  float: left;
  width: calc(100% - 40px);
  text-align: center;
  margin-top: 10px;
}

.ModalComponentHeaderIcon {
  float: left;
  width: 20px;
  margin-top: 10px;
}

.ModalComponentHeaderIconButton {
  float: left;
  color: white;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.ModalComponentBody {
  float: left;
  width: calc(100%);
  max-height: calc(100% - 100px);
}

.ModalComponentBodyContainer {
  float: left;
  width: calc(100%);
}

.SelectModal {
  width: 80% !important;
  max-height: 80% !important;
  top: calc(100% - 95%);
  left: calc(100% - 95%);
}

.ModalComponentBodyFocusable {
  height: 0;
  margin: 0;
  width: 0;
}

.ModalComponentBodyFocusableInput {
  height: 0;
  margin: 0;
  width: 0;
  background: transparent;
  border: transparent;
  outline: none;
}
</style>